.container {
    border: solid 1px #aaa;
    border-radius: 20px;
    padding: 8px;
    margin: 8px;
    display: flex;
    justify-content: space-around;
    align-items: center;

    button {
        background-color: #ddd;
        border: none;
        padding: 8px;
        border-radius: 8px;

        &:hover {
            background-color: #aaa;
            color: #fff;
            cursor: pointer;
        }
    }
}

.title {
    margin: 0;
    color: #aaa;
}